import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Input,
  Form,
  Button,
  message
} from 'antd'
const FormItem = Form.Item
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.productQuery, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  componentDidMount() {
    // 数据初始化
    const {modalData, update} = this.props
    // 判断是新增还是编辑
    if (modalData) {
      let reqObj = {...modalData}
      // 特殊处理数据
      update({modalData: {...reqObj}})
    }
  }
  handleSubmit = (e) => {
    const {form, modalData, handleChangeNum, update, handleGetList, searchParams, page, size} = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        let reqObj = {...values}
        console.log(reqObj)
        handleChangeNum({data: {...reqObj, productCode: modalData.productCode}}, () => {
          message.success('件装数修改成功')
          update({isShowModal: false})
          handleGetList({searchParams: {page, size, ...searchParams}})
        })
      }
    })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowModal: false})
  }

  render() {
    const {modalTitle, form: {getFieldDecorator}, modalTuttonLoading, modalData} = this.props
    const formItemLayout = {
      labelCol: {
        xs: {span: 24},
        sm: {span: 8}
      },
      wrapperCol: {
        xs: {span: 24},
        sm: {span: 10}
      }
    }
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className='operationModal'
        footer={null}
        width='520px'
      >
        <Form onSubmit={this.handleSubmit} autoComplete='off'>
          <Row>
            <Col span={24}>
              <div style={{height: '63px'}}>
                <FormItem {...formItemLayout} label={'件装数'}>
                  {getFieldDecorator('pieceNum', {
                    initialValue: modalData.pieceNum,
                    rules: [
                      {
                        required: true, message: '请填写'
                      }, {
                        pattern: /^[0-9]*$/,
                        message: '请输入正确的整数'
                      }
                    ]
                  })(
                    <Input maxLength='10'/>
                  )}
                </FormItem>
              </div>
            </Col>
          </Row>
          <Row
            type="flex"
            justify="center"
          >
            <Col>
              <Button style={{marginRight: '48px'}} onClick={this.handleCloseModal}>取消</Button>
              <Button type="primary" htmlType="submit" loading={modalTuttonLoading}>保存</Button>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



