import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  Input,
  DatePicker,
  message
  // Checkbox
} from 'antd'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSelect, CsxSearch, CsxMultipleSearch, CsxMultipleSelect} from 'Form'
import {PICKTYPE} from 'Mock'
import {
  API_PRODUCTS_KEYVALUES,
  API_PRODUCT_CATEGORYSONS,
  API_PRODUCT_GETPRODUCTKEYVALUES,
  API_PRODUCT_GETPRODUCTPURCHASEGROUPS,
  API_PRODUCT_GETPRODUCTSPLITGROUPS_KEYVALUE
} from 'api'

const {RangePicker} = DatePicker

@connect(
  state => ({...state.productQuery, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size} = this.props
    let reqObj = {...searchParams}
    reqObj.splitGroupCode && (reqObj.splitGroupCode = reqObj.splitGroupCode.data && reqObj.splitGroupCode.data.map(o => o.key)).join(',')
    handleGetList({searchParams: {...reqObj, page: 1, size}})
  }

  // 重置
  reset = () => {
    const {update} = this.props
    update({searchParams: {}})
  }
  render() {
    const {
      searchParams: {
        productCode,
        productName,
        productBarCode,
        bigCategoryCode,
        centreCategoryCode,
        smallCategoryCode,
        initDate,
        pickingSideFlag,
        purchaseGroupCode,
        splitGroupCode
        // supplierCode,
        // pickingSide
      },
      userLoation,
      searchParams
    } = this.props
    console.log(searchParams)
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={8}>
            <CsxMultipleSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label='商品编码'
              searchParamsKey='productCode'
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{data: productCode, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({productCode: value})
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label='商品名称'
              searchParamsKey='productName'
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{data: productName, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value, n) => {
                this.onInputChange({productName: value})
              }}
              onSelect={(value) => {
                this.onInputChange({productName: value})
              }}
            />
            {/* <div className='commonInput'>
              <label>商品名称：</label>
              <div className='box'>
                <Input value={productName} onChange={(e) => {
                  this.onInputChange({productName: e.target.value})
                }}/>
              </div>
            </div> */}
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>商品条码：</label>
              <div className='box'>
                <Input value={productBarCode} onChange={(e) => {
                  this.onInputChange({productBarCode: e.target.value})
                }} />
              </div>
            </div>
          </Col>
          {/* <Col span={8}>
            <div className='commonInput'>
              <label>供应商编码：</label>
              <div className='box'>
                <Input value={supplierCode} onChange={(e) => {
                  this.onInputChange({supplierCode: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>默认拣选面：</label>
              <div className='box'>
                <Input value={pickingSide} onChange={(e) => {
                  this.onInputChange({pickingSide: e.target.value})
                }}/>
              </div>
            </div>
          </Col> */}
          <Col span={16}>
            <Row type="flex">
              <CsxMultipleSelect
                dateApi={API_PRODUCTS_KEYVALUES}
                label='商品分类'
                placeholder='大类'
                searchParams={{type: '2'}}
                selectItem={{data: bigCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  searchParams.centreCategoryCode = {}
                  searchParams.smallCategoryCode = {}
                  this.onInputChange({bigCategoryCode: value})
                }}
                style={{width: 163}}
              />
              <CsxMultipleSelect
                placeholder='中类'
                dateApi={API_PRODUCT_CATEGORYSONS}
                searchParams={{parCode: bigCategoryCode && bigCategoryCode.value, level: '3'}}
                selectItem={{data: centreCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  searchParams.smallCategoryCode = {}
                  this.onInputChange({centreCategoryCode: value})
                }}
                noReady
                errMessage = '请先选择大类'
                style={{marginLeft: 10, width: 163}}
              />
              <CsxMultipleSelect
                placeholder='小类'
                dateApi={API_PRODUCT_CATEGORYSONS}
                searchParams={{parCode: centreCategoryCode && centreCategoryCode.value, level: '4'}}
                selectItem={{data: smallCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  this.onInputChange({smallCategoryCode: value})
                }}
                noReady
                errMessage = '请先选择中类'
                style={{marginLeft: 10, width: 163}}
              />
            </Row>
          </Col>
          <Col span={8}>
            <CsxMultipleSelect
              dateApi={API_PRODUCT_GETPRODUCTSPLITGROUPS_KEYVALUE}
              label='拆单分组'
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{data: splitGroupCode, key: 'value', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({splitGroupCode: value})
              }}
            />
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>创建时间：</label>
              <div className='box' style={{marginRight: 0}}>
                <RangePicker onChange={(moment, string) => {
                  this.onInputChange({initDate: moment})
                }}
                  format={'YYYY-MM-DD'}
                  value={initDate}
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <CsxSelect
              label='是否有拣选面'
              data={PICKTYPE}
              selectItem={{data: pickingSideFlag, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({pickingSideFlag: value})
              }}
            />
          </Col>
          <Col span={8}>
            <CsxMultipleSelect
              dateApi={API_PRODUCT_GETPRODUCTPURCHASEGROUPS}
              label='采购组'
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{data: purchaseGroupCode, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({purchaseGroupCode: value})
              }}
            />
          </Col>

          <Col span={24}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



