import * as actionType from './actionType'
import {getData} from 'util'
import {API_PRODUCTS_PAGE, API_PRODUCTS_UPDATE, API_PRODUCTS_UPDATESTATUS, API_PRODUCTS_EXPORT, API_PRODUCT_INITINCREMENTPRODUCTS, API_PRODUCTS_UPDATEPIECENUM, API_SYNCSPLITGROUP} from 'api'
import moment from 'moment'

//同步
export const update = (object) => ({
  type: actionType.PRODUCTQUERY_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.PRODUCTQUERY_UPDATE, data: {tableLoading: true}})
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      i === 'productName' ? reqObj[i] = reqObj[i].children : reqObj[i] = reqObj[i].value
      // reqObj[i] = reqObj[i].value
    }
  }
  if (reqObj.initDate && reqObj.initDate.length) {
    reqObj.createTimeStart = moment(reqObj.initDate[0]).format('YYYY-MM-DD')
    reqObj.createTimeEnd = moment(reqObj.initDate[1]).format('YYYY-MM-DD')
  }
  delete reqObj.initDate
  getData({api: API_PRODUCTS_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    let {page, size} = reqData.searchParams
    dispatch({
      type: actionType.PRODUCTQUERY_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.PRODUCTQUERY_FILE
    })
  })
}

// 编辑商品
export const handleEdit = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.PRODUCTQUERY_UPDATE, data: {modalTuttonLoading: true}})
  let api = API_PRODUCTS_UPDATE
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.PRODUCTQUERY_UPDATE,
      data: {
        modalTuttonLoading: false,
        isShowModal: false
      }
    })
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.PRODUCTQUERY_FILE
    })
  })
}

// 启用禁用仓库
export const handleOperator = (reqData, callBack) => (dispatch) => {
  getData({api: API_PRODUCTS_UPDATESTATUS, ...reqData}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.PRODUCTQUERY_FILE
    })
  })
}

// 导出
export const handleExport = (reqData, callBack) => (dispatch) => {
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  if (reqObj.initDate && reqObj.initDate.length) {
    reqObj.createTimeStart = moment(reqObj.initDate[0]).format('YYYY-MM-DD')
    reqObj.createTimeEnd = moment(reqObj.initDate[1]).format('YYYY-MM-DD')
  }
  delete reqObj.initDate
  getData({api: API_PRODUCTS_EXPORT, ...reqData}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.PRODUCTQUERY_FILE
    })
  })
}

// 同步商品
export const handleSync = (callBack) => (dispatch) => {
  getData({api: API_PRODUCT_INITINCREMENTPRODUCTS}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.PRODUCTQUERY_FILE
    })
  })
}

// 同步商品拆单分组
export const handleSylg = (callBack) => (dispatch) => {
  getData({api: API_SYNCSPLITGROUP}).then(data => {
    callBack && callBack(data.result)
  }).catch(err => {
    dispatch({
      type: actionType.PRODUCTQUERY_FILE
    })
  })
}

// 同步商品
export const handleChangeNum = (reqData, callBack) => (dispatch) => {
  getData({api: API_PRODUCTS_UPDATEPIECENUM, ...reqData}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.PRODUCTQUERY_FILE
    })
  })
}



