import React, {PureComponent} from 'react'
import {
  message,
  Button
} from 'antd'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {Import} from 'HOC'
import {API_PRODUCTS_IMPORT} from 'api'

@connect(
  state => ({...state.productQuery}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  handleExport = () => {
    const {handleExport, searchParams, page, size} = this.props
    console.log(searchParams)
    let reqObj = {...searchParams}
    reqObj.splitGroupCode && (reqObj.splitGroupCode = reqObj.splitGroupCode.data && reqObj.splitGroupCode.data.map(o => o.key)).join(',')
    handleExport({searchParams: {page, size, ...reqObj, productName: reqObj.productName ? reqObj.productName.children : ''}}, () => {
      message.success('导出成功')
    })
  }

  /* 下载模版 */
  handleDownLoad = (key) => {
    const downloadData = JSON.parse(localStorage.getItem('b2bWmsDownload'))
    console.log(downloadData)
    window.open(downloadData[key].downloadUrl)
  }

  /* 导入成功 */
  handleImportSuccess = res => {
    const {update, handleGetList, searchParams, page, size} = this.props
    let reqObj = {...searchParams}
    reqObj.splitGroupCode && (reqObj.splitGroupCode = reqObj.splitGroupCode.data && reqObj.splitGroupCode.data.map(o => o.key)).join(',')
    update({tableLoading: false})
    res && res.message === 'ok' && message.success('导入成功')
    handleGetList({searchParams: {page, size, ...reqObj}})
  }
  /* 报错导出 */
  handleExprotError = () => {
  }

    // 查询数据
  query = () => {
    const {handleGetList, searchParams, size} = this.props
    let reqObj = {...searchParams}
    reqObj.splitGroupCode && (reqObj.splitGroupCode = reqObj.splitGroupCode.data && reqObj.splitGroupCode.data.map(o => o.key)).join(',')
    handleGetList({searchParams: {...reqObj, page: 1, size}})
  }

    // 同步商品
  sync = () => {
    const {handleSync} = this.props
    handleSync(() => {
      this.query()
      message.success('同步商品成功')
    })
  }

    // 同步拆单分组
  split = () => {
    const {handleSylg} = this.props
    handleSylg(() => {
      this.query()
      message.success('正在同步拆单分组中，请稍后查看')
    })
  }
  render() {
    return (
      <div className={style['table-tool']}>
        <Button type="primary" className={style.btn} onClick={() => {
          this.handleExport()
        }}>导出</Button>
        <Button type="primary" className={style.btn} onClick={() => {
          this.handleDownLoad('T13')
        }}>模板下载</Button>
        <Import
          action={{api: API_PRODUCTS_IMPORT}}
          onOk={this.handleImportSuccess}
          onExprotError={this.handleExprotError}
          beforeImport={() => {
            const {update} = this.props
            update({tableLoading: true})
          }}
          onResponse={() => {
            const {update} = this.props
            update({tableLoading: false})
          }}
        >
          <Button type="primary" className={style.btn}>导入</Button>
        </Import>
        <Button type="primary" className={style.btn} onClick={this.sync} >同步商品</Button>
        <Button type="primary" className={style.btn} onClick={this.split} >同步拆单分组</Button>
      </div>
    )
  }
}



