import React, {PureComponent} from 'react'
import {
  Table,
  Modal,
  message,
  Icon,
  Popover
} from 'antd'
const confirm = Modal.confirm
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import JsBarcode from 'jsbarcode'

@connect(
  state => ({...state.productQuery, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '操作',
      width: 100,
      dataIndex: '',
      key: '',
      render: (text, record, index) => <div className="tableOperation">
        <span className='blue' onClick={() => {
          this.onEdit(text, record, index)
        }}>
          修改件装数
        </span>
      </div>
    }, {
      title: '商品编号',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode',
      render: (text, record, index) => <React.Fragment><span>{text}</span><Popover content={<img id={`key${text}`} style={{height: '50px'}} />} title={text} trigger="click"><Icon type="file-text" className='my-icon' onClick={this.handlePrint.bind(this, text)} /></Popover></React.Fragment>
    }, {
      title: '商品名称',
      width: 200,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '商品条码',
      width: 150,
      dataIndex: 'productBarCode',
      key: 'productBarCode',
      render: (text, record, index) => <React.Fragment><span>{text}</span><Popover content={<img id={`key${text}`} style={{width: '205px', height: '50px'}} />} title={text} trigger="click"><Icon type="file-text" className='my-icon' onClick={this.handlePrint.bind(this, text)} /></Popover></React.Fragment>
    }, {
      title: '基础单位',
      width: 150,
      dataIndex: 'basicUnit',
      key: 'basicUnit'
    }, {
      title: '商品规格',
      width: 150,
      dataIndex: 'productSpec',
      key: 'productSpec'
    }, {
      title: '商品状态',
      width: 150,
      dataIndex: 'productStatus',
      key: 'productStatus'
    }, {
      title: '默认拣选面',
      width: 150,
      dataIndex: 'pickingSide',
      key: 'pickingSide'
    }, {
      title: '保质期天数',
      width: 150,
      dataIndex: 'qualityGuaranteePeriod',
      key: 'qualityGuaranteePeriod'
    }, {
      title: '商品类型',
      width: 150,
      dataIndex: 'productTypeName',
      key: 'productTypeName'
    }, {
      title: '子商品',
      width: 100,
      dataIndex: 'sonProductFlagDesc',
      key: 'sonProductFlagDesc'
    }, {
      title: '商品母码',
      width: 100,
      dataIndex: 'productMotherCode',
      key: 'productMotherCode'
    }, {
      title: '件装数',
      width: 100,
      dataIndex: 'pieceNum',
      key: 'pieceNum'
    }, {
      title: '拆单分组',
      width: 200,
      dataIndex: 'splitGroupName',
      key: 'splitGroupName',
      render: (text, record) => <span>{record.splitGroupCode ? `${record.splitGroupCode}-${record.splitGroupName}` : ''}</span>
    }, {
      title: '大类',
      width: 150,
      dataIndex: 'bigCategoryName',
      key: 'bigCategoryName'
    }, {
      title: '中类',
      width: 150,
      dataIndex: 'centreCategoryName',
      key: 'centreCategoryName'
    }, {
      title: '小类',
      width: 150,
      dataIndex: 'smallCategoryName',
      key: 'smallCategoryName'
    }, {
      title: '创建时间',
      width: 150,
      dataIndex: 'createTime',
      key: 'createTime'
    }, {
      title: '最新更新时间',
      width: 100,
      dataIndex: 'updateTime',
      key: 'updateTime'
    }, {
      title: '更新人',
      width: 100,
      dataIndex: 'updateBy',
      key: 'updateBy'
    }]
  }

  handleOperator(text, record, index, type) {
    const {handleGetList, searchParams, page, size, handleOperator} = this.props
    confirm({
      title: '温馨提示',
      content: `是否${type ? '启用' : '禁用'}该条信息？`,
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleOperator({
          data: {
            id: record.id,
            warehouseStatus: type
          },
          urlParams: [record.id]
        }, () => {
          message.success('操作成功')
          handleGetList({searchParams: {page, size, ...searchParams}})
        })
      }
    })
  }
  //修改件装数
  onEdit(text, record) {
    const {update} = this.props
    update({modalData: record, isShowModal: true, modalTitle: '修改件装数'})
  }

  componentDidMount() {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (!userLoation || !userLoation.value) return
    let reqObj = {...searchParams}
    reqObj.splitGroupCode && (reqObj.splitGroupCode = reqObj.splitGroupCode.data && reqObj.splitGroupCode.data.map(o => o.key)).join(',')
    handleGetList({searchParams: {page, size, ...reqObj}})
  }

  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, userLoation} = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({searchParams: {page: 1, size: newProps.size, ...newProps.searchParams}})
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size} = this.props
    let reqObj = {...searchParams}
    reqObj.splitGroupCode && (reqObj.splitGroupCode = reqObj.splitGroupCode.data && reqObj.splitGroupCode.data.map(o => o.key)).join(',')
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({searchParams: {page, size, ...reqObj, isAsc: true, columns: sorter.field}})
      } else {
        handleGetList({searchParams: {page, size, ...reqObj, isAsc: false, columns: sorter.field}})
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({searchParams: {page: pagination.current, size: pagination.pageSize, ...reqObj}})
    }
  }

  // 打印条形码
  handlePrint = (text) => {
    setTimeout(() => {
      JsBarcode(`#key${text}`, text, {
        width: 1.5,
        height: 30,
        displayValue: false
      })
    })
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 'max-content', y: 400}}
          onChange={this.onChange}
          pagination={pagination}
        />
      </div>
    )
  }
}



